// @flow
import React from 'react';
import { Row, Col, Card } from 'react-bootstrap';

import cardImg4 from '../../assets/images/logo-copa.jfif';
// components
import PageTitle from '../../components/PageTitle';
import MensajeBienvenida from './MensajeBienvenida';

const CardWithImage2 = () => {
    return (
        <Card className="d-block">
            <Card.Img src={cardImg4} />
            <Card.Body>
                <Card.Title as="h5">Copa Airlines Colombia</Card.Title>
                <Card.Text>
                    Copa Airlines Colombia es una aerolínea comercial de pasajeros fundada y registrada bajo razón
                    social de Aero República S.A. en noviembre de 1992, ​​ es la segunda compañía aérea en Colombia por
                    pasajeros internacionales movilizados después de Avianca y la cuarta en tráfico total.
                </Card.Text>
            </Card.Body>
            <Card.Body>
                <Card.Link href="https://es.wikipedia.org/wiki/Copa_Airlines_Colombia">Wikipedia</Card.Link>
            </Card.Body>
        </Card>
    );
};

const Copa = (props) => {
    return (
        <React.Fragment>
            <PageTitle breadCrumbItems={[{ label: 'Copa', path: '/pages/Copa', active: true }]} title={''} />
            <Row>
                <Col lg={6}>
                    <MensajeBienvenida mensage={props.mensage} />
                </Col>
                <Col lg={6}>
                    {' '}
                    <CardWithImage2 />
                </Col>
            </Row>
        </React.Fragment>
    );
};

export default Copa;
